	module.exports = {"data":{"wordpressPost":{"title":"Seven Mother&#8217;s Day Gifts For Less Than $50","content":"<p><strong>Mother&#8217;s Day</strong> is just around the corner and you know that means &#8211; cards, hugs, declarations of love and if, you&#8217;re that way inclined, gifts. Of course, <strong>you&#8217;re not made of money though</strong> (and if you are please let us know how you made that happen?) so we figure you&#8217;ll appreciate a hot tip on a quality buy that will not break the bank.</p>\n<p>We reckon you can&#8217;t go past the following.</p>\n<img class=\"alignnone size-medium wp-image-1717\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17165630/clean-slate-JPG-147x300.jpg\" alt=\"\" width=\"147\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17165630/clean-slate-JPG-147x300.jpg 147w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17165630/clean-slate-JPG.jpg 448w\" sizes=\"(max-width: 147px) 100vw, 147px\" />\n<p><a href=\"http://mecca.com.au/mecca-cosmetica/clean-slate-micellar-water/V-027301.html?cgpath=brands-mecca#start=1\">Mecca Cosmetica Clean Slate Micellar Water, $29.</a></p>\n<p>This handy cleanser works, like really works, and all you have to do is soak a cotton pad and then gently wipe all over. Doing so removes makeup (even the waterproof variety) and it cleanses too. Nice.</p>\n<img class=\"alignnone size-medium wp-image-1718\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170056/Byredo-jpg-275x300.jpg\" alt=\"\" width=\"275\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170056/Byredo-jpg-275x300.jpg 275w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170056/Byredo-jpg.jpg 703w\" sizes=\"(max-width: 275px) 100vw, 275px\" />\n<p><a href=\"http://mecca.com.au/byredo/burning-rose-candle/V-021824.html?cgpath=brands-byredo#start=1\">Byredo Burning Rose Candle (70g), $39.Â </a></p>\n<p>Hand-made in France, this candle&#8217;s incredible fragrance smells all the world like an old-fashioned, country-style garden. It contains notes of rose, violet and leather and burns down to base notes of birch tree and ebony woods.</p>\n<img class=\"alignnone size-medium wp-image-1720\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170851/Maelin-JPG-300x205.jpg\" alt=\"\" width=\"300\" height=\"205\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170851/Maelin-JPG-300x205.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170851/Maelin-JPG-768x524.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17170851/Maelin-JPG.jpg 924w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"http://mecca.com.au/malin-goetz/rum-soap-bar/I-020849.html?cgpath=brands-malin#start=1\">Malin + Goetz Rum Bar Soap, $23.</a></p>\n<p>It&#8217;s not the most romantic of gifts but it is practical and at this price you can afford a couple. Plus, it&#8217;s gorgeous on skin &#8211; it cleanses like a soap should, but will also your mum baby soft from top to toe &#8211; and it&#8217;s quirky rum scent is delightful.</p>\n<img class=\"alignnone size-medium wp-image-1721\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17171733/The-Balm-JPG-276x300.jpg\" alt=\"\" width=\"276\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17171733/The-Balm-JPG-276x300.jpg 276w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17171733/The-Balm-JPG-768x835.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17171733/The-Balm-JPG.jpg 866w\" sizes=\"(max-width: 276px) 100vw, 276px\" />\n<p><a href=\"https://www.sephora.com.au/products/thebalm-autobalm-hawaii-palette\">The Balm Autobalm Hawaii Palette, $41.50.</a></p>\n<p>So maybe you can&#8217;t take your mother to Hawaii right now. If you give her this palette she can at least pretend? And that&#8217;s almost like being in Hawaii right? Right? Okay not really. But it does contain a seriously pretty and super high-quality blush, highlighting eyeshadow, eyelid eyeshadow and a crease/liner eyeshadow.</p>\n<img class=\"alignnone size-medium wp-image-1722\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17172323/Zoeva-JPG-300x235.jpg\" alt=\"\" width=\"300\" height=\"235\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17172323/Zoeva-JPG-300x235.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17172323/Zoeva-JPG-768x603.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17172323/Zoeva-JPG.jpg 998w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"https://www.sephora.com.au/products/zoeva-coral-spectrum-blush-palette\">Zoeva Coral Spectrum Blush Palette, $36.</a></p>\n<p>We&#8217;re huge fans of this makeup brand which deserves to be better known than it is. It&#8217;s well-priced and the quality of the products is peerless. It&#8217;s very popular in the makeup room at the Nine Network where a swag of Australia&#8217;s biggest celebrities are made up every day. This blush palette is particularly sweet &#8211; you (ahem, we mean your mum!) will love it.</p>\n<img class=\"alignnone size-medium wp-image-1725\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173029/IT-JPG-216x300.jpg\" alt=\"\" width=\"216\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173029/IT-JPG-216x300.jpg 216w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173029/IT-JPG.jpg 633w\" sizes=\"(max-width: 216px) 100vw, 216px\" />\n<p><a href=\"https://www.sephora.com.au/products/it-cosmetics-vitality-lip-flush-4-in-1-reviver-lipstick-stain/v/damsel\">IT Cosmetics Vitality Lip Flush 4-in-1 Reviver Lipstick Stain, $28.</a></p>\n<p>If you&#8217;ve not yet sampled IT Cosmetics then we recommend you do. This range, the brainchild of the very clever and also awesome Jamie Kerns, is so very good it&#8217;s hard to put into words. Of course, we include this particular lipstick in that statement. It provides a naturally, pretty flush with the colour of a lipstick and the conditioning feel of a balm.</p>\n<img class=\"alignnone size-medium wp-image-1726\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173740/10_06_15_KKMascara_0063B_15-bd-225x300.jpg\" alt=\"\" width=\"225\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173740/10_06_15_KKMascara_0063B_15-bd-225x300.jpg 225w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173740/10_06_15_KKMascara_0063B_15-bd-768x1022.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17173740/10_06_15_KKMascara_0063B_15-bd-769x1024.jpg 769w\" sizes=\"(max-width: 225px) 100vw, 225px\" />\n<p><a href=\"https://www.priceline.com.au/covergirl-katy-kat-mascara-10-5-ml\">Covergirl Katy Kat Mascara, $18.95.</a></p>\n<p>The clever brush combined with a very high-tech formula means this mascara provides super long, curled, sexy lashes. It provides intense, rich, dark colour and comes in both Black and Perry Blue (yes &#8211; this is the Katy Perry collection). Our advice &#8211; go for black. No offence Katy. You know we love you.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/17164812/Mecca-JPG1.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"5fcd51a4-0bb9-5bff-aee3-57c45530b06b"}}

